import React from 'react';

import {
  makeStyles,
  List,
  ListItem,
  Collapse,
  ListItemText,
} from '@material-ui/core';
import clsx from 'clsx';
import { useSelector } from 'react-redux';

import { AppState } from '../../../state';
import { AnimatedExpandedIcon } from '../common/AnimatedExpandedIcon';
import { ChapterFavList } from './chapter-fav-list';
import { KuralFavList } from './kural-fav-list';

const useStyles = makeStyles((theme) => ({
  root: {},
  collapsableGroup: {},
  list: {
    '& .sub-header': {
      textAlign: 'left',
      color: 'brown',
      lineHeight: '2rem',
      padding: `0px 0 0 ${theme.spacing(0.5)}px`,
    },
  },
  listItemTitle: {
    '& .toggle-icon-container': {
      marginLeft: theme.spacing(1.5),
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
    },
  },
  favCount: {
    fontSize: '0.8rem',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
}));

type FavoritesViewerProps = {
  className?: string;
  expand?: boolean;
};

export const FavoritesViewer: React.FC<FavoritesViewerProps> = (
  props: FavoritesViewerProps
) => {
  const classes = useStyles();
  const favorites = useSelector((st: AppState) => st.user.favorites);
  const KURAL_IDS = (favorites || [])
    .filter((it) => it.category === 'kural')
    .map((it) => it.id);
  const CHAPTER_IDS = (favorites || [])
    .filter((it) => it.category === 'chapter')
    .map((it) => it.id);
  const [isKuralVisible, setKuralVisible] = React.useState<boolean>(
    props.expand ?? true
  );
  const [isChapterVisible, setChapterVisible] = React.useState<boolean>(
    props.expand ?? false
  );

  const noRecordsFound = <h1>None Found</h1>;

  const GroupTitleMaker = (
    isVisible: boolean,
    onClick: () => void,
    title: React.ReactNode,
    favCount: React.ReactNode,
    collapseContent?: React.ReactNode
  ) => (
    <>
      <ListItem
        selected={isVisible}
        className={classes.listItemTitle}
        button
        onClick={onClick}>
        <ListItemText inset={true} primary={title} />
        {!isVisible && <span className={classes.favCount}>{favCount}</span>}
        <div className={`toggle-icon-container`}>
          <AnimatedExpandedIcon isExpanded={isVisible} />
        </div>
      </ListItem>
      <Collapse
        in={isVisible}
        style={{ paddingBottom: isVisible ? 8 : 0 }}
        className={classes.collapsableGroup}
        timeout='auto'>
        {collapseContent || noRecordsFound}
      </Collapse>
    </>
  );
  return (
    <div className={clsx(props.className, classes.root)}>
      <List className={classes.list} disablePadding>
        {GroupTitleMaker(
          isKuralVisible,
          () => {
            setKuralVisible(!isKuralVisible);
          },
          'Kurals',
          KURAL_IDS.length ? KURAL_IDS.length : null,
          KURAL_IDS.length && <KuralFavList kuralIds={KURAL_IDS} />
        )}
        {GroupTitleMaker(
          isChapterVisible,
          () => {
            setChapterVisible(!isChapterVisible);
          },
          'Chapters',
          CHAPTER_IDS.length ? CHAPTER_IDS.length : null,
          CHAPTER_IDS.length && <ChapterFavList chapterIds={CHAPTER_IDS} />
        )}
      </List>
    </div>
  );
};
